.app-layout .ant-layout-header {
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.app-layout .ant-layout-footer {
  background: #f0f2f5;
  padding: 24px 0;
  text-align: center;
}

/* 语言切换按钮样式 */
.language-switcher {
  margin-left: 16px;
}

/* 响应式导航 */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }
}

/* 首页样式 */
.home-banner {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 60px 0;
  margin-bottom: 30px;
  border-radius: 8px;
  color: white;
}

.home-banner h1 {
  font-size: 36px;
  margin-bottom: 16px;
}

.home-banner p {
  font-size: 18px;
  margin-bottom: 24px;
}

.feature-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.feature-card .ant-card-body {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.feature-card .ant-card-meta-title {
  margin-bottom: 8px;
}

.feature-card .ant-card-meta-description {
  flex: 1;
}

/* 模块页面样式 */
.module-header {
  margin-bottom: 30px;
  padding-bottom: 16px;
  border-bottom: 2px solid #f0f0f0;
}

.module-header h2 {
  margin: 0;
  font-size: 28px;
  color: #333;
}

/* 按钮样式 */
.primary-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  padding: 10px 24px;
  font-size: 16px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.primary-btn:hover {
  opacity: 0.9;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}